:root {
  --body-font: "Inter", sans-serif;
  --text-color: #555555;
  --text-hover-color: #888888;
  --title-color: #111111;
  --normal-font-size: 16px;
  --primary-color: #42ffd2;
  --primary-hover-color: #00f5ba;
  --secondary-color: #ffffff;
  --secondary-hover-color: #d9d9d9;
  --light-color: #eeeeee;
  --light-hover-color: #c8c8c8;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

body {
  background: #eeeeee;
  font-size: var(--normal-font-size);
  font-family: var(--body-font);
  line-height: 1.5;
  color: var(--text-color);
}

button {
  background: none;
  border: none;
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
}

input {
  font-family: var(--body-font);
  font-size: 1rem;
  background: none;
  border: none;
  line-height: 1.5;
}

::placeholder {
  color: #999999;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: var(--title-color);
}

h1,
h2,
h4,
h6 {
  font-weight: 500;
  color: var(--title-color);
}

h1 {
  font-weight: 400;
  font-size: 48px;
  line-height: 1.208;
}

h2 {
  font-size: 22px;
}

h4 {
  line-height: 1.188;
}

h6 {
  font-size: 12px;
  line-height: 1.25;
}

.hr {
  border: none;
  height: 1px;
  background-color: #cccccc;
  margin-bottom: 2px;
}

.container {
  width: 85%;
  max-width: 1392px;
  margin: 0 auto;
}

.btn {
  font-weight: 500;
  line-height: 1.188;
  padding: 10.5px 12px;
  background: var(--btn-bg);
  border-radius: 8px;
  color: var(--btn-color, var(--title-color));
  position: relative;
  z-index: 1;
  overflow: hidden;
  transition: 500ms;
}
.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--btn-bg-hover);
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 500ms;
  z-index: -1;
}
.btn:hover::before {
  transform: scaleX(1);
  transform-origin: left;
}
.btn--primary {
  --btn-bg: var(--primary-color);
  --btn-bg-hover: var(--primary-hover-color);
}
.btn--secondary {
  --btn-bg: var(--secondary-color);
  --btn-bg-hover: var(--secondary-hover-color);
}
.btn--light {
  --btn-bg: var(--light-color);
  --btn-bg-hover: var(--light-hover-color);
}
.btn--outline {
  --btn-bg: none;
  --btn-bg-hover: none;
}
.btn--outline:hover {
  color: #ffff;
}

.mr-16 {
  margin-right: 16px;
}

.notif-dot {
  position: relative;
  display: inline-block;
}
.notif-dot::after {
  content: "";
  width: 10px;
  height: 10px;
  background: #ff4242;
  border-radius: 50%;
  box-sizing: content-box;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(30%, -30%);
  z-index: 1;
}
.notif-dot--outline::after {
  width: 8px;
  height: 8px;
  border: 3px solid #eeeeee;
  transform: translate(3px, -3px);
}
.notif-dot--check::after {
  width: 16px;
  height: 16px;
  transform: translate(0, 0);
  background-image: url("img/check.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: var(--primary-color);
}

.section-margin {
  margin-bottom: 32px;
}
@media (min-width: 768px) {
  .section-margin {
    margin-bottom: 56px;
  }
}

.section-heading {
  display: grid;
  grid-template-columns: 1fr auto;
  margin-bottom: 32px;
  column-gap: 24px;
  row-gap: 16px;
}
.section-heading p {
  grid-row: 2;
}
.section-heading button {
  align-self: center;
  grid-row: span 2;
}

.no-scroll {
  overflow: hidden;
  height: 100vh;
}
@media (min-width: 576px) {
  .no-scroll {
    padding-right: 15px;
  }
}

.swiper {
  width: 100%;
  height: 100%;
}

.intro-cards-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 24px;
}
@media (min-width: 768px) {
  .intro-cards-wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
}

.intro-card {
  background-color: var(--bg-color, #ffffff);
  padding: 32px;
  border-radius: 8px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 32px;
}
.intro-card:hover .intro-card__img {
  transform: translateY(-15%);
}
.intro-card__body {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.intro-card__icon {
  width: 32px;
  height: 32px;
}
.intro-card__button {
  align-self: flex-start;
}
.intro-card__img {
  align-self: center;
  justify-self: center;
  flex-shrink: 0;
  transition: 250ms;
}
.intro-card--upcoming:nth-child(1) {
  --bg-color: #ffd8e6;
  --text-color: #ff005c;
}
.intro-card--upcoming:nth-child(2) {
  --bg-color: #f2d8ff;
  --text-color: #aa00ff;
}
.intro-card--upcoming .intro-card__button {
  --btn-color: var(--text-color);
  --btn-bg-hover: var(--text-color);
  border: 1px solid var(--text-color);
}
.intro-card--collections:nth-child(1) {
  --bg-color: #ffe6d8;
}
.intro-card--collections:nth-child(2) {
  --bg-color: #d8ecff;
}
.intro-card--collections:nth-child(3) {
  --bg-color: #ffd8eb;
}
.intro-card--collections:nth-child(4) {
  --bg-color: #fff4d8;
}
.intro-card--collections .intro-card__button {
  --text-color: #111111;
  --btn-color: var(--text-color);
  --btn-bg-hover: var(--text-color);
  border: 1px solid var(--text-color);
}

.nft-card {
  position: relative;
  border-radius: 8px;
  overflow: hidden;
  user-select: none;
}
.nft-card > img {
  width: 100%;
}
.nft-card__desc {
  background: var(--bg-color);
  backdrop-filter: blur(12px);
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 24px;
  gap: 12px;
}
.nft-card__desc span:nth-child(1) {
  color: var(--primary-color);
  font-weight: 500;
  font-size: 12px;
  line-height: 1.25;
  text-transform: uppercase;
}
.nft-card--ending-1 {
  --bg-color: rgba(227, 227, 220, 0.64);
}
.nft-card--ending-2 {
  --bg-color: rgba(143, 1, 0, 0.64);
}
.nft-card--ending-3 {
  --bg-color: rgba(22, 22, 22, 0.64);
}
.nft-card--ending-4 {
  --bg-color: rgba(1, 227, 225, 0.64);
}
.nft-card--ending-2, .nft-card--ending-3 {
  --text-color-1: #ffffff;
  --text-color-2: #cccccc;
}
.nft-card--ending-1, .nft-card--ending-4 {
  --text-color-1: #111111;
  --text-color-2: #777777;
}
.nft-card--ending .nft-card__desc {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.nft-card--ending .nft-card__desc span:nth-child(2),
.nft-card--ending .nft-card__desc span:nth-child(3),
.nft-card--ending .nft-card__desc span:nth-child(4) {
  color: var(--text-color-1);
}
.nft-card--ending .nft-card__desc span:nth-child(5),
.nft-card--ending .nft-card__desc span:nth-child(6) {
  color: var(--text-color-2);
}
.nft-card--ending .nft-card__desc span:nth-child(2),
.nft-card--ending .nft-card__desc span:nth-child(4),
.nft-card--ending .nft-card__desc span:nth-child(6) {
  justify-self: flex-end;
}
.nft-card--ending .nft-card__desc span:nth-child(2) {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.25;
  text-transform: uppercase;
}
.nft-card--ending .nft-card__desc span:nth-child(3),
.nft-card--ending .nft-card__desc span:nth-child(4) {
  font-weight: 500;
  line-height: 1.188;
}
.nft-card--learn-1 {
  --bg-color: rgba(184, 141, 208, 0.64);
}
.nft-card--learn-2 {
  --bg-color: rgba(222, 200, 163, 0.64);
}
.nft-card--learn-3 {
  --bg-color: rgba(122, 181, 161, 0.64);
}
.nft-card--learn-4 {
  --bg-color: rgba(51, 113, 198, 0.64);
}
.nft-card--learn .nft-card__desc {
  display: flex;
  flex-direction: column;
}
.nft-card--learn .nft-card__desc span:nth-child(2) {
  color: #ffffff;
}

.nav {
  margin-top: 36px;
}
.nav__container {
  display: flex;
  align-items: center;
}
.nav__toggler {
  height: 19px;
  display: grid;
  place-items: center;
  margin-right: 24px;
  position: relative;
  z-index: 9999;
  cursor: pointer;
}
@media (min-width: 992px) {
  .nav__toggler {
    display: none;
  }
}
.nav__toggler span {
  display: block;
  width: 24px;
  height: 3px;
  background: #111111;
  transition: 500ms;
}
.nav__toggler span:nth-child(1),
.nav__toggler span:nth-child(3) {
  position: absolute;
  left: 0;
}
.nav__toggler span:nth-child(1) {
  transform: translateY(calc(-100% - 5px));
}
.nav__toggler span:nth-child(3) {
  transform: translateY(calc(100% + 5px));
}
.nav__toggler--active span:nth-child(1) {
  transform: rotate(45deg);
}
.nav__toggler--active span:nth-child(2) {
  transform: translateX(-15px);
  background: transparent;
}
.nav__toggler--active span:nth-child(3) {
  transform: rotate(-45deg);
}
.nav__logo {
  margin-right: auto;
}
.nav__logo img {
  max-width: none;
}
@media (min-width: 992px) {
  .nav__logo {
    margin-right: 98px;
  }
}
.nav__menu {
  position: fixed;
  background-color: #ffffff;
  top: 0;
  left: 0;
  width: 90%;
  max-width: 250px;
  height: 100%;
  padding: 100px 32px 0;
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-end;
  transform: translateX(-100%);
  transition: 500ms;
  z-index: 9998;
  gap: 24px;
}
.nav__menu--open {
  transform: translateX(0);
}
@media (min-width: 992px) {
  .nav__menu {
    width: 100%;
    max-width: none;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    background: none;
    position: static;
    padding: 0;
    transform: translateX(0);
  }
}
.nav__search {
  background: #eeeeee;
  border-radius: 8px;
  padding: 8px 12px;
  width: 100%;
  max-width: 448px;
  margin-bottom: 20px;
}
@media (min-width: 992px) {
  .nav__search {
    background: #ffffff;
    margin-bottom: 0;
  }
}
.nav__links-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 992px) {
  .nav__links-wrapper {
    flex-direction: row;
    justify-content: flex-end;
    gap: 22px;
    margin-right: 22px;
  }
}
.nav__link {
  font-weight: 500;
  line-height: 1.188;
  display: block;
  padding: 22px;
  position: relative;
}
.nav__link:hover {
  background: #eeeeee;
  border-radius: 8px;
}
@media (min-width: 992px) {
  .nav__link {
    padding: 0;
  }
  .nav__link::after {
    content: "";
    position: absolute;
    background-color: currentColor;
    left: 0;
    bottom: -2px;
    width: 100%;
    height: 2px;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 500ms;
  }
  .nav__link:hover {
    background: none;
    border-radius: 0;
  }
  .nav__link:hover::after {
    transform: scaleX(1);
    transform-origin: left;
  }
}
.nav__user img {
  width: 32px;
  max-width: none;
}

